import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { Container, Card, Row, Col, Table, Badge, Button, Spinner } from 'react-bootstrap';
import { getOrderById, cancelOrder } from '../services/orderService';

const orderStatusVariants = {
  'pending': 'warning',
  'processing': 'info',
  'shipped': 'primary',
  'delivered': 'success',
  'cancelled': 'danger',
  'paid': 'success',
  'failed': 'danger'
};

const orderStatusLabels = {
  'pending': 'Ch xc nhn',
  'processing': 'ang x l',
  'shipped': 'ang giao hng',
  'delivered': ' giao hng',
  'cancelled': ' hy',
  'paid': ' thanh ton',
  'failed': 'Thanh ton tht bi'
};

const paymentMethods = {
  'cod': 'Thanh ton khi nhn hng (COD)',
  'banking': 'Chuyn khon ngn hng',
  'momo': 'V MoMo',
  'vnpay': 'VNPay',
  'zalopay': 'ZaloPay'
};

const OrderDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [cancellingOrder, setCancellingOrder] = useState(false);
  
  // Hm format gi tin
  const formatPrice = (price) => {
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(price);
  };
  
  // Hm format ngy
  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    const date = new Date(dateString);
    return new Intl.DateTimeFormat('vi-VN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };
  
  // Ly thng tin chi tit n hng
  useEffect(() => {
    const fetchOrderDetail = async () => {
      try {
        setLoading(true);
        setError(null);
        
        const response = await getOrderById(id);
        
        if (response.success) {
          setOrder(response.order);
        } else {
          setError(response.message || 'Khng th ti thng tin n hng');
        }
      } catch (err) {
        setError('Li kt ni: ' + err.message);
      } finally {
        setLoading(false);
      }
    };
    
    fetchOrderDetail();
  }, [id]);
  
  // Hm x l hy n hng
  const handleCancelOrder = async () => {
    if (!window.confirm('Bn c chc chn mun hy n hng ny?')) {
      return;
    }
    
    try {
      setCancellingOrder(true);
      const result = await cancelOrder(id);
      
      if (result.success) {
        setOrder({ ...order, status: 'cancelled' });
        alert(result.message);
      } else {
        alert(result.message || 'Khng th hy n hng');
      }
    } catch (err) {
      alert('Li khi hy n hng: ' + err.message);
    } finally {
      setCancellingOrder(false);
    }
  };
  
  if (loading) {
    return (
      <Container className="my-5 text-center">
        <Spinner animation="border" role="status">
          <span className="visually-hidden">ang ti...</span>
        </Spinner>
        <p className="mt-3">ang ti thng tin n hng...</p>
      </Container>
    );
  }
  
  if (error) {
    return (
      <Container className="my-5">
        <div className="alert alert-danger" role="alert">
          {error}
        </div>
        <div className="text-center mt-3">
          <Button variant="primary" onClick={() => navigate('/my-orders')}>
            Quay li danh sch n hng
          </Button>
        </div>
      </Container>
    );
  }
  
  if (!order) {
    return (
      <Container className="my-5 text-center">
        <h3>Khng tm thy n hng</h3>
        <p>n hng khng tn ti hoc bn khng c quyn xem.</p>
        <Button variant="primary" onClick={() => navigate('/my-orders')}>
          Quay li danh sch n hng
        </Button>
      </Container>
    );
  }
  
  return (
    <Container className="my-4">
      <div className="mb-4">
        <Button variant="secondary" onClick={() => navigate('/my-orders')}>
          &larr; Quay li danh sch n hng
        </Button>
      </div>
      
      <h2 className="mb-4">Chi Tit n Hng #{order._id}</h2>
      
      <Row className="mb-4">
        <Col md={6}>
          <Card className="mb-3">
            <Card.Header as="h5">Thng Tin n Hng</Card.Header>
            <Card.Body>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">M n hng:</Col>
                <Col md={7}>{order._id}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Ngy t hng:</Col>
                <Col md={7}>{formatDate(order.createdAt)}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Trng thi:</Col>
                <Col md={7}>
                  <Badge bg={orderStatusVariants[order.status] || 'secondary'}>
                    {orderStatusLabels[order.status] || order.status}
                  </Badge>
                </Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Tng tin:</Col>
                <Col md={7} className="fw-bold text-danger">
                  {formatPrice(order.totalAmount)}
                </Col>
              </Row>
              <Row className="mb-2">
                <Col md={5} className="fw-bold">Phng thc thanh ton:</Col>
                <Col md={7}>
                  {paymentMethods[order.paymentMethod] || order.paymentMethod}
                </Col>
              </Row>
              
              {/* Hin th thng tin thanh ton nu c */}
              {order.paymentDetails && (
                <>
                  {order.paymentDetails.transactionId && (
                    <Row className="mb-2">
                      <Col md={5} className="fw-bold">M giao dch:</Col>
                      <Col md={7}>{order.paymentDetails.transactionId}</Col>
                    </Row>
                  )}
                  {order.paymentDetails.paidAt && (
                    <Row className="mb-2">
                      <Col md={5} className="fw-bold">Thi gian thanh ton:</Col>
                      <Col md={7}>{formatDate(order.paymentDetails.paidAt)}</Col>
                    </Row>
                  )}
                </>
              )}
            </Card.Body>
          </Card>
          
          {/* Nt hy n hng */}
          {(order.status === 'pending' || order.status === 'processing') && (
            <div className="mt-3">
              <Button
                variant="danger"
                onClick={handleCancelOrder}
                disabled={cancellingOrder}
              >
                {cancellingOrder ? (
                  <>
                    <Spinner
                      as="span"
                      animation="border"
                      size="sm"
                      role="status"
                      aria-hidden="true"
                      className="me-2"
                    />
                    ang hy n hng...
                  </>
                ) : (
                  'Hy n hng'
                )}
              </Button>
            </div>
          )}
        </Col>
        
        <Col md={6}>
          <Card>
            <Card.Header as="h5">Thng Tin Khch Hng</Card.Header>
            <Card.Body>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">H tn:</Col>
                <Col md={8}>{order.name}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">Email:</Col>
                <Col md={8}>{order.email}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">S in thoi:</Col>
                <Col md={8}>{order.phone}</Col>
              </Row>
              <Row className="mb-2">
                <Col md={4} className="fw-bold">a ch:</Col>
                <Col md={8}>{order.address}</Col>
              </Row>
              {order.city && (
                <Row className="mb-2">
                  <Col md={4} className="fw-bold">Thnh ph:</Col>
                  <Col md={8}>{order.city}</Col>
                </Row>
              )}
              {order.note && (
                <Row className="mb-2">
                  <Col md={4} className="fw-bold">Ghi ch:</Col>
                  <Col md={8}>{order.note}</Col>
                </Row>
              )}
            </Card.Body>
          </Card>
        </Col>
      </Row>
      
      <Card className="mb-4">
        <Card.Header as="h5">Sn Phm  t</Card.Header>
        <Card.Body>
          <Table responsive striped bordered>
            <thead>
              <tr>
                <th style={{ width: '80px' }}>STT</th>
                <th style={{ width: '100px' }}>Hnh nh</th>
                <th>Sn phm</th>
                <th style={{ width: '100px' }}>n gi</th>
                <th style={{ width: '100px' }}>S lng</th>
                <th style={{ width: '150px' }}>Thnh tin</th>
              </tr>
            </thead>
            <tbody>
              {order.products && order.products.map((item, index) => (
                <tr key={index}>
                  <td className="text-center">{index + 1}</td>
                  <td>
                    {item.productId && item.productId.image ? (
                      <img 
                        src={item.productId.image} 
                        alt={item.productId.name} 
                        style={{ width: '80px', height: '80px', objectFit: 'cover' }}
                      />
                    ) : (
                      <div 
                        className="bg-light d-flex align-items-center justify-content-center" 
                        style={{ width: '80px', height: '80px' }}
                      >
                        No image
                      </div>
                    )}
                  </td>
                  <td>
                    {item.productId ? (
                      <Link to={`/product/${item.productId._id}`}>
                        {item.productId.name}
                      </Link>
                    ) : (
                      'Sn phm khng cn tn ti'
                    )}
                  </td>
                  <td className="text-end">
                    {item.productId && formatPrice(item.productId.price)}
                  </td>
                  <td className="text-center">{item.quantity}</td>
                  <td className="text-end">
                    {item.productId && formatPrice(item.productId.price * item.quantity)}
                  </td>
                </tr>
              ))}
              <tr>
                <td colSpan="5" className="text-end fw-bold">Tng cng:</td>
                <td className="text-end fw-bold text-danger">
                  {formatPrice(order.totalAmount)}
                </td>
              </tr>
            </tbody>
          </Table>
        </Card.Body>
      </Card>
    </Container>
  );
};

export default OrderDetail; 