import axios from 'axios';

// ng dn API c th cn iu chnh ty theo cu trc backend
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api/orders';

// To axios instance vi cu hnh chung
const axiosInstance = axios.create({
  baseURL: API_URL,
  withCredentials: true
});

// Thm interceptor  t ng thm token vo header
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

/**
 * Ly danh sch n hng ca ngi dng  ng nhp
 * @param {Object} options - Cc ty chn lc (status, page, limit)
 * @returns {Promise} Danh sch n hng vi phn trang
 */
export const getMyOrders = async (options = {}) => {
  try {
    // To query parameters t options
    const params = new URLSearchParams();
    if (options.status) params.append('status', options.status);
    if (options.page) params.append('page', options.page);
    if (options.limit) params.append('limit', options.limit);
    
    console.log(`Gi API: ${API_URL}/?${params.toString()}`);
    const response = await axiosInstance.get(`/?${params.toString()}`);
    
    return {
      success: true,
      orders: response.data.orders || [],
      pagination: response.data.pagination || {
        total: 0,
        page: 1,
        limit: 10,
        totalPages: 0
      }
    };
  } catch (error) {
    console.error('Li khi ly danh sch n hng:', error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n my ch',
      orders: [],
      pagination: {
        total: 0,
        page: 1,
        limit: 10,
        totalPages: 0
      }
    };
  }
};

/**
 * Ly chi tit n hng theo ID
 * @param {string} orderId - ID ca n hng
 * @returns {Promise} Chi tit n hng
 */
export const getOrderById = async (orderId) => {
  try {
    console.log(`Gi API: ${API_URL}/${orderId}`);
    const response = await axiosInstance.get(`/${orderId}`);
    return {
      success: true,
      order: response.data.order || {}
    };
  } catch (error) {
    console.error(`Li khi ly chi tit n hng ID ${orderId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n my ch',
      order: {}
    };
  }
};

/**
 * Hy n hng
 * @param {string} orderId - ID ca n hng cn hy
 * @returns {Promise} Kt qu hy n hng
 */
export const cancelOrder = async (orderId) => {
  try {
    console.log(`Gi API: ${API_URL}/${orderId}/cancel`);
    const response = await axiosInstance.patch(`/${orderId}/cancel`);
    return {
      success: true,
      message: response.data.message || 'n hng  c hy thnh cng',
      order: response.data.order || {}
    };
  } catch (error) {
    console.error(`Li khi hy n hng ID ${orderId}:`, error);
    return {
      success: false,
      message: error.response?.data?.message || 'Khng th kt ni n my ch'
    };
  }
}; 